@keyframes ani-mouse {
  0% {
    opacity: 1;
    top: 15%;
  }
  15% {
    opacity: 1;
    top: 70%;
  }
  50% {
    opacity: 0;
    top: 70%;
  }
  100% {
    opacity: 0;
    top: 29%;
  }
}

.scroll-btn {
  position: absolute;
  bottom: 25px;
}

.scroll-btn > *:hover, .scroll-btn > *:focus, .scroll-btn > *.active {
  color: #ffffff;
}

.scroll-btn > *:hover, .scroll-btn > *:focus, .scroll-btn > *:active, .scroll-btn > *.active {
  opacity: 0.8;
  filter: alpha(opacity=80);
}

.scroll-btn .mouse {
  position: relative;
  display: block;
  width: 35px;
  height: 55px;
  margin: 0 auto 20px;
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 23px;
}

.scroll-btn .mouse > * {
  position: absolute;
  display: block;
  top: 29%;
  left: 50%;
  width: 3px;
  height: 10px;
  margin: -1.5px 0 0 -1.5px;
  background: white;
  border-radius: 5px;
  animation: ani-mouse 3.5s linear infinite;
}

